.header {
    margin: 0px 0px 30px 0px;
    background: -webkit-linear-gradient(top, #808083, #4b4b4e);
    // border-radius: 30px 30px 0 0;

    &__container {
        padding: 5px 15px 5px 15px;
        display: flex;
        justify-content: space-between;
    }

    &__logo {
        display: flex;
        align-items: center;
    }

    &__auth {
        display: flex;
        align-items: center;
        color: #ffffff;
        padding: 0 5px;
        margin: 5px 0;
    }

    &__log {
        display: flex;
        align-items: center;
        >:not(:last-child) {
            margin: 0px 10px 0px 0px;
        }
    }

    &__image img {
        width: 45px;
        height: 45px;
        border-radius: 50%;
    }

    &__name {
        margin: 0px 0px 0px 25px;
        font-size: 24px;
        font-weight: 700;
        color: #fff;
    }
}
